.el-table__body tr.hover-row.current-row>td,
.el-table__body tr.hover-row.el-table__row--striped.current-row>td,
.el-table__body tr.hover-row.el-table__row--striped>td,
.el-table__body tr.hover-row>td{background-color:#F5F7FA}*/
/*表单提交按钮居中显示*/
.el-dialog__footer {text-align: center;}
/*横向表单信息样式调整 by heliping*/
.el-input-group__append, .el-input-group__prepend {
    background-color: #F3F6FA;
    color: #5B6371;
    border-color: #D8DEE6;
	padding: 0 15px;
}
.el-input.is-disabled .el-input__inner {border-color: #D8DEE6;}
.el-input.is-disabled .el-input__inner {color: #97A3B4;}
.el-input__inner { height: 35px; line-height: 35px;}
/*Checkbox by heliping*/
.el-checkbox__inner {
	width: 18px; 
	height: 18px; 
	border-radius:4px; 
	border-color: #C7CED6;
	/*background-color: #FFFFFF !important;
	background-color: #DFE5EA !important;*/
}
.el-checkbox__input.is-focus .el-checkbox__inner {
	border-color:#8996F1;
}
.el-checkbox__inner::after {
	left: 6px;
	top: 3px;
}
.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner
{
	background-color: #8996F1;
	border-color: #8996F1;
}
/*Radio by heliping*/
.el-radio__inner {
	width: 18px; 
	height: 18px;
}
.el-radio__inner::after {
    width: 8px;
    height: 8px;
}
/*分页样式调整*/
.el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li
{
	color: #97A3B4;
	background-color: #FFFFFF;
    border: solid 1px #d1d8de;
    border-radius:3px;
}
.el-pagination__sizes .el-input .el-input__inner:hover,
.el-pagination.is-background .el-pager li:not(.disabled):hover
 { border-color: #6585DF; color: #6585DF;}
.el-pagination.is-background .el-pager li:not(.disabled).active {background-color: #6585DF; color: #FFFFFF;}
/*表单页标题调整 heliping*/
.el-form--label-top .el-form-item__label {padding-top: 5px; padding-bottom: 0px; line-height:25px;}
.el-form-item {
    margin-bottom: 5px;
}
.el-textarea__inner {padding-left: 5px; padding-right: 5px;}
/**/
.searchlist{
	background:rgba(255,255,255,1);
	-webkit-box-shadow:0px 5px 10px 0px rgba(243,244,250,1);
	        box-shadow:0px 5px 10px 0px rgba(243,244,250,1);
	border-radius:3px;
	border:1px solid #D8DEE6;
}
.searchlist .el-form-item__label {
	background-color: #F3F6FA;
	border-right: 1px solid #D8DEE6;
	border-top-left-radius: 3px;
	border-bottom-left-radius: 3px;
}
.searchlist input {border:none !important; border-radius:none !important;}
.searchlist input:focus { color: #6585DF; }
/*手风琴样式修改*/
.el-collapse-item__header {border-bottom: none; font-size: 14px; font-weight: bold; color: rgba(59,66,78,1);}
.el-collapse-item__header:before {
    width: 20px;
    height: 3px;
    display: table;
    line-height: 0;
    content: '';
    background-color: #6585DF;
    top: 0px;
	position: absolute;
}
.el-collapse-item {
	padding-left: 25px;
    padding-right: 7px;
    background-color: #FFF;
    -webkit-box-shadow:0px 5px 10px 0px rgba(223,229,234,1);
            box-shadow:0px 5px 10px 0px rgba(223,229,234,1);
	border-radius:4px;
	margin-bottom: 8px;
}
.el-collapse-item__arrow {font-weight: bold;}
.el-dropdown {cursor: pointer;}
/*------------------------左侧菜单样式调整 By Heliping------------------------*/
.navbar-static-side{
	float: left;
    width: 220px;
    height: 100%;
    position: fixed;
    background: -webkit-gradient(linear,left bottom, left top,from(rgba(160, 120, 240, 1)),to(rgba(65, 105, 218, 1)));
    background: linear-gradient(360deg,rgba(160, 120, 240, 1) 0%,rgba(65, 105, 218, 1) 100%);
    z-index: 1000;
}
.slimScrollDiv{
	position: relative;
    width: auto;
    height: 92%;
}
.navbarbg, .navbarbg a {
    color: #FFF;
}
.navbarbg {
    height: 40px;
    text-align: right;
    padding-right: 15px;
    cursor: pointer;
}
.navs>li {
    margin: 5px 10px;
}
.navs > li > a {
    color: #fff;
    display: block;
    padding: 14px 10px 14px 10px;
    border-radius:4px;
}
.navs > li > a:hover,.navs > li > a.router-link-active{background:rgba(97,131,226,.7);}
.navs > li > a i[class*=icon-] {
    font-size: 16px;
}
.navs > li > a i {
    margin-right: 6px;
}
.minimalize-styl-2 {
    font-size: 14px;
    color: #FFFFFF;
    line-height: 40px;
    cursor: pointer;
}
.nav>li {
    margin: 0px 10px;
}
.nav > li > a i[class*=icon-] {
    font-size: 16px;
}
.nav > li > a i {
    margin-right: 6px;
}
/*左侧树菜单*/
.v-resize {
	position: relative;
}
.v-resize:before {
	content: '';
	position: absolute;
	width:10px;
	height: 100%;
	top: 0px;
	left: -5px;
	z-index: 55;
	cursor: e-resize;
	background-color: #F5F8FB;
}
.lefttreebg {
	border-top: 1px solid #ebeef5;
	border-left: 1px solid #ebeef5;
	border-right: 1px solid #ebeef5;
	background-color: #FFFFFF;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
}
.left_treebg {
  width: 100%;
  height: 400px;
  overflow: scroll;
  background-color: #FFF;
}
.left_tree_title {
	font-size:12px;
	color:rgba(116,125,140,1);
	line-height:44px;
	height: 44px;
	padding-left: 15px;
	padding-right: 15px;
	cursor: pointer;
	border-bottom: 1px solid #ebeef5;
}
/*Element-UI-Tree树菜单*/
.el-tree .el-tree-node__content>.el-tree-node__expand-icon {
  padding: 2px;
}
.el-tree .el-icon-caret-right { 
  font-size: 14px;
  width: 17px;
  height: 17px;
  line-height:12px;
  font-weight: lighter;
  color: #A2ABBF;
  border-radius: 3px;
  margin-top: -2px;
  margin-right: 5px;
  position: relative;
  z-index: 30;
}
.el-tree .el-tree-node .icon-file-normal {/*文件夹合并时图标*/
  color: #6585DF;
  font-family: 'hxqheam';
  content: "\E9FA";
  font-size: 20px;
}
.el-tree .el-tree-node.is-expanded>.el-tree-node__content .icon-file-normal:before {/*文件夹打开时图标*/
  font-family: 'hxqheam';
  content: "\E9FB";
}
.el-tree .el-tree-node>.el-tree-node__content .icon-file-text {/*最后子级图标颜色*/
  color: #92BDFF;
}
.el-tree-node [class^="icon-"], .el-tree-node [class*=" icon-"] {margin-right: 5px;}
.p15 {padding:10px 15px;}
