button {border: none; padding:6px 10px 5px 10px; border-radius:4px;}
button,html input[type="button"],input[type="reset"],input[type="submit"] {
    cursor: pointer;
    -webkit-appearance: button
}
button[disabled],html input[disabled] {
    cursor: default
}
button:hover {opacity: .8;}
button:focus,.btn-outline {
    color: inherit;
    -webkit-transition: all .5s;
    transition: all .5s;
}
input[type="checkbox"],input[type="radio"] {
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}
button:-moz-focus-inner,input:-moz-focus-inner {
    padding: 0;
    border: 0
}
textarea {
    overflow: auto;
    vertical-align: top
}
input[type="text"],
input[type="password"],
textarea{
    height:35px; padding:0px 5px;
    background-color:#FFFFFF;
    border-radius:3px;
    border:1px solid #DFE5EA;
    /* border: none;
    border-bottom:1px solid #DFE5EA; */
}
.el-input-number {line-height: 33px;}
/*Select 样式调整*/
.el-select .el-input .el-select__caret { font-weight: bold;}
.el-select .el-input .el-select__caret.is-reverse {color:#6585DF;}
.el-select-dropdown__item.selected {color: #6585DF;}
.el-select-dropdown__item.hover, .el-select-dropdown__item:hover {
    background-color: #F1F3FD; color: #576FAE;
}
.el-select .el-input__inner:focus {border-color: #A4ACE7;}
/*文本框样式调整*/
textarea { height: auto; }
input[type="text"]:focus,
input[type="password"]:focus,
textarea:focus,button:focus,
.el-select .el-input.is-focus .el-input__inner,
.el-textarea__inner:focus {
    outline:none;
    -webkit-appearance: none;
    -webkit-box-shadow:0px 5px 10px 0px #f1f4ff;
            box-shadow:0px 5px 10px 0px #f1f4ff;
    border-color: #A4ACE7;
}
/*开关样式*/
.el-switch.is-checked .el-switch__core {
    border-color: #13ce66;
    background-color: #13ce66;
}
.el-switch {line-height: 40px; height: 40px;}
/*操作按钮样式*/
[class*=" btn-"],[class^="btn-"] {
    color: #FFFFFF;
    font-size: 14px;
}
[class*=" btn-"]:focus,[class^="btn-"]:focus {
    color: #FBFDFF;
}
[class*=" btn-"] i,[class^="btn-"] i {margin-right:5px;}
[class*=" btn-large"],[class^="btn-large"]{
    height:36px;
    font-size: 16px;
    line-height:20px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius:6px;
    margin-left: 5px;
    margin-right: 5px;
}
.btn-default {
    background:rgba(162,174,190,1);
}
.btn-primarys {
    background:rgba(101,133,223,1);
}
.btn-green {
    background-color: #25b99e;
    border-color: #25b99e;
}
.btn-bule {
    background-color: #2fa5e5;
    border-color: #2fa5e5;
}
.btn-red {
    background-color: #e3517c;
    border-color: #e3517c;
}
.btn-primarys {
    background-color: #5d7fde;
    border-color: #5d7fde;
}
