/*右侧对Table操作项按钮*/
.btn-refresh,.btn-outline {
    height: 32px;
    padding-right: 10px;
    padding-left: 10px;
    line-height: 27px;
    padding-top: 3px;
    cursor: pointer;
}
.btn-refresh{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}
.btn-outline {
    padding-right: 5px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}
