}
/*按钮图标划出效果显示按钮文字*/
.el-collapse-item{
    position: relative;
}
.table-func {
    position: absolute; top: 10px; right: 40px;
}
.table-func button {
    width: 45px; -webkit-transition: width 400ms ease, background 400ms ease; transition: width 400ms ease, background 400ms ease;
}
.table-func button font {
    display: none;
}
.table-func button:hover {
    width: 80px;
}
.table-func button:hover font {
    display: inline-block;
}
.table-funcb {
    position: absolute;
    top: -50px;
    right: 0px;
}


/*Table List By heliping*/
table {
	width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
}
