}
.table th,.table td {border-bottom: 1px solid #f0f0f0; padding: 10px 5px; color: #5B6371;}
.table th {color:#747D8C; font-size: 12px;}
.table tr:nth-child(even) {background-color: #F5F7FA;}
.table tr:hover {background-color: #F8F9FF;}



